.home {
    min-height: 100vh;
    background-image: url('../assets/background62.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.primer-div {
    padding-top: 10vh;
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    min-height: 100vh;
    width: 100vw;

    // background-image: url('../assets/background6.jpg');
    // background-repeat: no-repeat;
    // background-size: cover;
    // background-position: center;
    // background-attachment: fixed;

    @media (max-width: 768px) {
        flex-direction: column;
        align-items: center;
    }
}

.home-title {
    width: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0 0 5%;
    flex-direction: column;

    position: relative;
    left: -100%;
    /* Inicia fuera de la pantalla en la izquierda */
    animation: slideInFromLeft 2.5s forwards;
    /* Aplica la animación */


    @media (max-width: 768px) {
        width: 90%;
        font-size: 30pt;
        text-align: center;
        padding: 2% 20%;
    }

    @media (max-width: 480px) {
        font-size: 20pt;
        padding: 0% 10% 0 10%;
    }
}

.home-title-title-button {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 15px;
    padding: 5vh 5vw;
    display: flex;
    flex-direction: column;
    align-items: end;

    @media (max-width: 768px) {
        align-items: center;
    }
}



.card-3d {
    height: 80vh;
    width: 45%;

    @media (max-width: 768px) {
        width: 90%;
        height: 70vh;
    }

    @media (max-width: 480px) {
        height: 60vh;
    }


}

.card-3d-card {
    transform: translatey(0px);
    animation: float 6s ease-in-out infinite;
}

.drag {
    width: 100px;
    position: absolute;
    top: 75%;
    left: 73%;
    opacity: 0;
    /* La imagen inicia con opacidad 0 (invisible) */
    animation: fadeEffect 2s 10;
    /* Aplicamos la animación */
}

@keyframes fadeEffect {
    0% {
        opacity: 0;
    }

    /* Inicio de la animación: opacidad 0 (invisible) */
    50% {
        opacity: 1;
    }

    /* Punto medio de la animación: opacidad 1 (visible) */
    100% {
        opacity: 0;
    }

    /* Fin de la animación: opacidad 0 (invisible) */
}

.segundo-div {
    //background-color: black;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.02) 0%, rgba(0, 0, 0, 1) 15%, rgb(0, 0, 0) 85%, rgba(38, 10, 10, 0.02) 100%);

    color: white;
    display: flex;
    flex-direction: column;
    min-height: 110vh;
    align-items: center;
    justify-content: center;
    padding: 30vh 0;

    // background-image: url('../assets/background4.jpg');
    // background-repeat: no-repeat;
    // background-size: cover;
    // background-position: center;

}

.segundo-div-primer-item {
    width: 100%;
    min-height: 50vh;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    padding: 35px 0;


}

.home-item {
    font-size: 30pt;
}

.ficha-home {
    background-color: rgba(0, 0, 0, 0.8);
    width: 250px;
    height: 350px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: 10px;
    text-align: center;
    padding: 30px;
    transition: transform 0.3s ease-in-out;
    
    @media (max-width: 480px) {
        max-width: 80vw;
        height: 200px;
    }
}

.ficha-home-image {
    //width: 100%;
    height: 60%;
}

.ficha-home:hover {
    transform: rotate(-5deg);
    background: linear-gradient(145deg, rgba(222, 3, 3, 0.4) 0%, rgba(2, 16, 74, 0.4) 80%);
}

.segundo-div-segundo-item {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5vh;
}

.item-segundo-div-segundo-item {
    margin-top: 30px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 768px) {}

    @media (max-width: 480px) {}
}

.item-text {
    flex: 1;
    transition-duration: 0.3s;
}

.item-text:hover {
    font-size: 23pt;
}

.item-text-first {
    text-align: end;
}

.coins-logo-small {
    width: 70px;
    transition-duration: 0.5s;

    @media (max-width: 768px) {}

    @media (max-width: 480px) {
        width: 50px;
    }
}

.tercer-div {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    color: white;
    text-align: center;
}

.phase {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 20px;
    padding: 25px 20px;
}

.li-roadmap {
    font-size: 25pt;
    font-family: MiFuente;
    transition-duration: 0.5s;

    @media (max-width: 768px) {
        font-size: 18pt;
    }

    @media (max-width: 480px) {
        font-size: 15pt;
    }
}

.li-roadmap:hover {
    font-size: 28pt;
}




@keyframes slideInFromLeft {
    to {
        left: 0;
        /* Mueve el div a su posición final (0% desde la izquierda) */
    }
}

@keyframes float {
    0% {
        //box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
        transform: translatey(0px);
    }

    50% {
        //box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2);
        transform: translatey(-20px);
    }

    100% {
        //box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
        transform: translatey(0px);
    }
}